
.cartcontrol {
  font-size: 0;
}
.cartcontrol .move-enter,
.cartcontrol .move-leave-to {
  opacity: 0;
  -webkit-transform: translate3d(30px, 0, 0) rotate3d(0, 0, 1, 180deg);
          transform: translate3d(30px, 0, 0) rotate3d(0, 0, 1, 180deg);
}
.cartcontrol .move-enter-active,
.cartcontrol .move-leave-active {
  transition: all 0.3s linear;
}
.cartcontrol .move-enter-to,
.cartcontrol .move-leave {
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0) rotate3d(0, 0, 0, 0);
          transform: translate3d(0, 0, 0) rotate3d(0, 0, 0, 0);
}
.cartcontrol .cart-decrease,
.cartcontrol .cart-add {
  display: inline-block;
  font-size: 24px;
  line-height: 24px;
  color: #00a0dc;
}
.cartcontrol .cart-count {
  display: inline-block;
  vertical-align: top;
  width: 12px;
  line-height: 24px;
  text-align: center;
  font-size: 10px;
  color: #93999f;
}


