
#app .tab {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 40px;
  border-bottom: 1px solid rgba(7,17,27,0.1);
  position: relative;
}
#app .tab:after {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  content: "";
  border-top: 1px solid rgba(7,17,27,0.1);
}
#app .tab .tab-item {
  -webkit-box-flex: 1;
      -ms-flex: auto;
          flex: auto;
  text-align: center;
  line-height: 40px;
  font-size: 14px;
  color: #4d555d;
}
#app .tab .active {
  color: #f01414;
}


